  text-align: center;
  font-size: 0;
  transform: translate3d(0, 0, 0);
  overflow: auto;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
}

[data-modal]:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

