  position: absolute;
  left: 0;
  top: -1px;
  opacity: 0;
  width: 16px;
  height: 16px;
}

.checkbox input:checked ~ .checkbox__icon {
  opacity: 1;
}

.checkbox__text:before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 22px;
  height: 22px;
  background-color: #363636;
}

.checkbox__icon {
  position: absolute;
  top: 50%;
