/*Sandwich*/
.toggle-menu {
  position: relative;
  float: right;
  width: 32px;
  height: 32px;
  display: none;
  z-index: 100;
  border: none;
  background-color: transparent;
  margin-top: 10px;
}

@media only screen and (max-width: 1023px) {
  .toggle-menu {
    display: block;
    margin-top: 2px;
  }
}

.toggle-menu:before {
  content: "";
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
}

.toggle-menu:focus {
  outline: none;
}

.sandwich {
  width: 32px;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 200;
  top: 50%;
  transform: translateY(-50%);
}

.sw-topper {
  display: block;
  position: relative;
  transform-origin: left top;
  width: 32px;
  height: 3px;
  background: #fff;
  border: none;
  transition: transform 0.5s;
  margin-bottom: 5px;
}

@media only screen and (max-width: 599px) {
  .sw-topper {
    margin-bottom: 4px;
  }
}

.header.is-fixed .sw-topper,
.header.is-open-menu .sw-topper {
  background: #363636;
}

.sw-bottom {
  overflow: hidden;
  display: block;
  position: relative;
}

.sw-bottom__inner {
  display: block;
  position: relative;
  width: 32px;
  height: 3px;
  transform-origin: center center;
  background: #fff;
  border: none;
  transition: transform 0.3s ease-in-out, opacity .5s ease-in-out;
  margin-bottom: 5px;
}

.header.is-fixed .sw-bottom__inner,
.header.is-open-menu .sw-bottom__inner {
  background: #363636;
}

@media only screen and (max-width: 599px) {
  .sw-bottom__inner {
    margin-bottom: 4px;
  }
}

.sw-footer {
  display: block;
  position: relative;
  width: 32px;
  height: 3px;
  transform-origin: left;
  background: #fff;
